import eis from "./const";
import fire from "./fire";

const initialState = eis;

const enemyBulletReducer = (state = initialState, action) => {
  const id = action.id;
  switch (action.type) {
    case "SELECT":
      return {
        ...state,
        [id]: {
          key: state[id].key,
          b6: { x: action.cord.x - 200, y: action.cord.y - 90 },
          color: state[id].color,
        },
      };
    case "FIRE":
      return fire(state, action);
    case "CANCEL":
     
      return {
        ...state,
        [id]: {
          key: state[id].key,
          b6: null,
          color: state[id].color,
        },
      };
    default:
      return state;
  }
};

export default enemyBulletReducer;
