import eis from "./const";
import move from "./move";
import attack from "./attack";
import explode from "./explode";

const initialState = eis;

const enemyReducer = (state = initialState, action) => {
  const id = action.id;

  switch (action.type) {
    case "ENEMYMOVE":
      return move(state, action);
    case "SELECT":
      return {
        ...state,
        [id]: {
          key: state[id].key,
          x: state[id].x,
          y: state[id].y,
          status: "ATTACKING", //stop moving
          angle: 0,
          AP: 1, // enter attacking phase
          dummyy: state[id].y, //record current y, keep moving
          dummyx: state[id].x, //record current x, keep moving
          color: state[id].color,
         
        },
      };
    case "ENEMYATTACK":
      return attack(state, action);
    // case "ENEMYEXPLODE":

    //   return explode(state,action)
    case "VANISH":
      return { ...state, [id]: null };
    default:
      return state;
  }
};

export default enemyReducer;
