import React, { PureComponent } from "react";
import Auxi from "../../auxi/auxi";
import { connect } from "react-redux";
import Ship from "../../components/glx/ship";
import Bullet from "../../components/glx/bullet";
import Enemy from "../../components/glx/enemy";
import EnemyAttack from "../../components/glx/enemyattack";
import music from "../../utility/music";

class InGame extends PureComponent {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    music.start();
  }
  componentDidUpdate() {}
  render() {
    /*hold enemy state in an array, used for collision detetion
    and enemymove. the original state is an array because of  
    enemy attack logic*/

    const MOVARR = [];
    for (let i = 0; i <= 45; i++) {
      if (this.props[i]) {
        if (this.props[i].status === "MOVING") {
          MOVARR.push(this.props[i]);
        }
      }
    }

    const BUARR = [];
    for (let i = 100; i <= 145; i++) {
      if (this.props[i].b6) {
        BUARR.push(this.props[i]);
      }
    }

    const ATKARR = [];
    for (let i = 0; i <= 45; i++) {
      if (this.props[i]) {
        if (this.props[i].status === "ATTACKING") {
          ATKARR.push(this.props[i]);
        }
      }
    }

    return (
      <Auxi>
        <Ship
          width={this.props.width}
          height={this.props.height}
          shipx={this.props.shipx}
          shipMove={this.props.shipMove}
          bu={BUARR}
          cancelEnemyBullet={this.props.cancelEnemyBullet}
          shipVanish={this.props.shipVanish}
          shipResume={this.props.shipResume}
          shipStatus={this.props.shipStatus}
        />
        <Bullet
          width={this.props.width}
          height={this.props.height}
          bullety={this.props.bullety}
          attack={this.props.attack}
          shipx={this.props.shipx}
          enemies2={MOVARR}
          vanish={this.props.vanish}
          explode={this.props.explode}
          bulletStatus={this.props.bulletStatus}
          passId={this.passId}
          YB={60}
          YD={25}
          enemies={ATKARR}
          goal={this.props.goal}
          enemyExplode={this.props.enemyExplode}
          cancelEnemyBullet={this.props.cancelEnemyBullet}
          shipStatus={this.props.shipStatus}
        />
        <Enemy
          width={this.props.width}
          height={this.props.height}
          XB={70}
          XD={40}
          YB={60}
          YD={25}
          S={5}
          enemies={MOVARR}
          enemyMove={this.props.enemyMove}
        />
        <EnemyAttack
          width={this.props.width}
          height={this.props.height}
          XB={70}
          XD={40}
          YB={60}
          YD={25}
          S={5}
          enemies={ATKARR}
          enemies2={MOVARR}
          enemyAttack={this.props.enemyAttack}
          pickAttacker={this.props.pickAttacker}
          shipx={this.props.shipx}
          bu={BUARR}
          fire={this.props.fire}
        />
      </Auxi>
    );
  }
}

