let counter = 0;
let direction = "RIGHT";
const S = 5;

const dummyMove = (state, dir) => {
  const target = {};
  for (let i = 0; i <= 45; i++) {
    target[i] = state[i]
      ? {
          key: state[i].key,
          x: state[i].x,
          y: state[i].y,
          status: state[i].status,
          angle: state[i].angle,
          AP: state[i].AP,
          dummyy: state[i].dummyy,
          dummyx: dir === "RIGHT" ? state[i].dummyx + S : state[i].dummyx - S,
          color: state[i].color,
          b1: state[i].b1,
          b2: state[i].b2,
          b3: state[i].b3,
          b4: state[i].b4,
          b5: state[i].b5,
        }
      : null;
  }
  return target;
};

const mapArr = (state, action, dir) => {
  const target = {};
  action.arr.forEach((el) => {
    target[el] = {
      key: state[el].key,
      x: dir === "RIGHT" ? state[el].x + S : state[el].x - S,
      y: state[el].y,
      status: state[el].status,
      angle: state[el].angle,
      AP: state[el].AP,
      dummyy: null,
      dummyx: null,
      color: state[el].color,
    
    };
  });
  return target;
};

export default (state, action) => {
  let target;
  target = {
    ...dummyMove(state, direction),
    ...mapArr(state, action, direction),
  };
  if (direction === "RIGHT") {
    counter++;
  }
  if (direction === "LEFT") {
    counter--;
  }
  if (counter === 12) {
    direction = "LEFT";
  }
  if (counter === 0) {
    direction = "RIGHT";
  }
  return target;
};
