const mapArr = (state, action) => {
  const target = {};

  action.arr.forEach((el, id) => {
    switch (el <= 22) {
      case true:
        target[el] = {
          key: el,
          b6:
          state[el].b6.y > 400
              ? null  
              : { x: state[el].b6.x + 2, y: state[el].b6.y + 2 },
          color: state[el].color,
        };
    }
  });
  return target;
};

export default (state, action) => {
  let target;
  target = { ...state, ...mapArr(state, action) };
  return target;
};
