const bxS1 = Math.random() + 2;
const bxS2 = Math.random() + 2;
const bxS3 = Math.random() + 2;
const bxS4 = Math.random() + 2;
const bxS5 = Math.random() + 2;

const mapArr = (state, action) => {
  const target = {};
  const exS = Math.random() * 2+3;
  action.arr.forEach((el, id) => {
    switch (el <= 22) {
      case true:
        switch (state[el].AP) {
          case 1:
            target[el] = {
              key: el,
              x: state[el].x,
              y: state[el].y,
              status: "ATTACKING",
              angle: state[el].angle - (3 * Math.PI) / 180,
              AP: state[el].angle > -1.25 * Math.PI ? 1 : 2,
              dummyy: state[el].dummyy,
              dummyx: state[el].dummyx,
              color: state[el].color,
              
            };
            break;

          case 2:
            target[el] = {
              key: el,
              x: state[el].x + 4 + (action.shipx - state[el].x) / 100,
              y: state[el].y + 1,
              status: "ATTACKING",
              angle: state[el].angle,
              AP:
                state[el].x - 40 + 40 * Math.cos(state[el].angle) > action.shipx // enemyx > shipx
                  ? 3
                  : 2,
              dummyy: state[el].dummyy,
              dummyx: state[el].dummyx,
              color: state[el].color,
            
            };
            break;

          case 3:
            target[el] = {
              key: el,
              x:
                state[el].y > 400
                  ? state[el].dummyx
                  : state[el].x + 3 + (action.shipx - state[el].x) / 100,
              y: state[el].y > 400 ? 0 : state[el].y + 1.5,
              status: "ATTACKING",
              angle:
                state[el].y > 400 ? 0 : state[el].angle - (1 /80) * Math.PI,
              AP: state[el].y > 400 ? 4 : 3,
              dummyy: state[el].dummyy,
              dummyx: state[el].dummyx,
              color: state[el].color,
              
            };
            break;

          case 4:
            target[el] = {
              key: el,
              x: state[el].dummyx,
              y:
                state[el].y === state[el].dummyy
                  ? state[el].y
                  : state[el].y + 1,
              status: state[el].y === state[el].dummyy ? "MOVING" : "ATTACKING",
              angle: 0,
              AP: 4,
              dummyy: state[el].dummyy,
              dummyx: state[el].dummyx,
              color: state[el].color,
             
            };
            break;
          default:
        }
        break;
      default:
    }
  });
  return target;
};

export default (state, action) => {
  let target;
  target = { ...state, ...mapArr(state, action) };
  return target;
};
